import React, { useState, useEffect } from 'react';
import { 
  Spinner, 
  SpinnerWrapper, 
  LoadingMessage, 
  LoadingDots, 
  Dot, 
  ProgressBar, 
  ProgressFill
} from "./Loading.styled"

interface LoadingProps {
  loading?: boolean;
  message?: string;
  showProgress?: boolean;
  type?: 'default' | 'enhanced';
}

const Loading = ({ 
  loading = true, 
  message = " ...", 
  showProgress = false,
  type = 'default'
}: LoadingProps) => {
  const [progress, setProgress] = useState(0);
  const [currentMessage, setCurrentMessage] = useState(message);

  useEffect(() => {
    if (!loading) return;

    if (showProgress) {
      const interval = setInterval(() => {
        setProgress(prev => {
          if (prev >= 90) return prev; // 90% 
          return prev + Math.random() * 10;
        });
      }, 200);

      return () => clearInterval(interval);
    }

    //    ( )
    if (showProgress) {
      const messages = [
        "  ...",
        "   ...",
        "   ..."
      ];

      let messageIndex = 0;
      const messageInterval = setInterval(() => {
        setCurrentMessage(messages[messageIndex % messages.length]);
        messageIndex++;
      }, 3000);

      return () => clearInterval(messageInterval);
    }
  }, [loading, showProgress]);

  if (!loading) return null;


  if (type === 'enhanced') {
    return (
      <SpinnerWrapper>
        <LoadingMessage>{currentMessage}</LoadingMessage>
        {showProgress && (
          <ProgressBar>
            <ProgressFill $progress={progress} />
          </ProgressBar>
        )}
        <LoadingDots>
          <Dot $delay={0} />
          <Dot $delay={0.2} />
          <Dot $delay={0.4} />
        </LoadingDots>
      </SpinnerWrapper>
    );
  }

  //  
  return (
    <SpinnerWrapper>
      <LoadingMessage>{message}</LoadingMessage>
      <LoadingDots>
        <Dot $delay={0} />
        <Dot $delay={0.2} />
        <Dot $delay={0.4} />
      </LoadingDots>
    </SpinnerWrapper>
  );
}

export default Loading