import React, { useState, useEffect, useMemo, useCallback, Suspense } from "react";
import * as S from "./Onboarding.styled";
import { usePopularSales, useSearchSale } from "../hook/useSales";
import { useNavigate, useLocation } from "react-router-dom";
import OnboardingCard from "../components/OnboardingCard";
import { Footer, NoMenuBar } from "../style/common.styled";
import { Product } from "../type/product";
// import searchIcon from "../resource/img/search.svg";
import Loading from "../components/Loading";
import { useAuth } from "../context/AuthContext";
import { useSubmitReview } from "../hook/useReviews";
import { useToast } from "../context/ToastContext";

const OnboardingContent = () => {
  const { data: products } = usePopularSales(60);
  const [selectedIds, setSelectedIds] = useState<number[]>([]);
  const navigate = useNavigate();
  const location = useLocation();
  const { token, me } = useAuth();
  const submitReviewMutation = useSubmitReview();
  const [isSubmitting, setIsSubmitting] = useState(false);
  const { showToast } = useToast();

  //   
  const [searchKeyword, setSearchKeyword] = useState('');
  const [debouncedSearchKeyword, setDebouncedSearchKeyword] = useState('');
  const [isSearching, setIsSearching] = useState(false);

  //  
  useEffect(() => {
    const timer = setTimeout(() => {
      setDebouncedSearchKeyword(searchKeyword);
    }, 500);

    return () => clearTimeout(timer);
  }, [searchKeyword]);

  //     
  const { data: searchResults } = useSearchSale(
    debouncedSearchKeyword && isSearching ? debouncedSearchKeyword : ''
  );

  //  6       
  const flowingProducts = useMemo(() => {
    if (!products || products.length === 0) return [];

    // 6  
    const groups: Product[][] = [];
    for (let i = 0; i < products.length; i += 6) {
      groups.push(products.slice(i, i + 6));
    }

    //        (8 )
    //        
    return groups.map((group, groupIndex) => ({
      products: [...group, ...group, ...group, ...group, ...group, ...group, ...group, ...group], // 8 
      direction: (groupIndex % 2 === 0 ? 'right' : 'left') as 'right' | 'left' //   ->,   ->
    }));
  }, [products]);

  const toggleSelect = useCallback((id: number) => {
    setSelectedIds(
      (prev) =>
        prev.includes(id)
          ? prev.filter((pid) => pid !== id)
          : prev.length < 5
            ? [...prev, id]
            : prev // 5  
    );
  }, []);



  //  
  const handleSearch = useCallback(() => {
    if (!searchKeyword.trim()) {
      setIsSearching(false);
      return;
    }
    setIsSearching(true);
  }, [searchKeyword]);

  const handleKeyPress = useCallback((e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === 'Enter') {
      handleSearch();
    }
  }, [handleSearch]);

  const handleInputChange = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    setSearchKeyword(e.target.value);
    //     
    if (!e.target.value.trim()) {
      setIsSearching(false);
    }
  }, []);

  const handleComplete = async () => {
    if (!token || !me) {
      showToast(" .", "error");
      navigate("/static/Login", { state: { from: location.pathname } });
      return;
    }

    if (selectedIds.length < 5) {
      showToast(" 5  .", "warning");
      return;
    }

    setIsSubmitting(true);

    try {
      //       /        .
      let successCount = 0;
      const failed: number[] = [];
      for (const salesId of selectedIds) {
        try {
          await submitReviewMutation.mutateAsync({
            review: {
              salesId,
              rating: 5,
              reviewComment: null,
            },
            token: token,
          });
          successCount += 1;
          //         ( )
          await new Promise((r) => setTimeout(r, 80));
        } catch {
          failed.push(salesId);
        }
      }

      if (failed.length === 0) {
        showToast("  !    .", "success");
      } else if (successCount > 0) {
        showToast(` . : ${successCount}, : ${failed.length}` as string, "warning");
      } else {
        showToast("   .  .", "error");
      }
      if (successCount > 0) {
        navigate("/static");
      }
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <NoMenuBar>
      <S.OnboardingWrapper>

        <S.Title>   ( 5)</S.Title>
        <S.SelectedCount>
          {selectedIds.length}/5 
        </S.SelectedCount>

        {/*   */}
        <S.SearchContainer>
          <S.SearchSection>
            <S.SearchTitle>  </S.SearchTitle>
            <S.SearchSubtitle>  !</S.SearchSubtitle>
            <S.SearchBoxWrapper>
              <S.SearchInput
                type="text"
                value={searchKeyword}
                onChange={handleInputChange}
                placeholder="  "
                onKeyDown={handleKeyPress}
              />
              <S.SearchButton onClick={handleSearch}>

              </S.SearchButton>
            </S.SearchBoxWrapper>
          </S.SearchSection>

          {isSearching && (
            <S.SearchResults>
              <S.SearchResultsTitle> </S.SearchResultsTitle>
              {!searchResults ? (
                <S.NoResults> ...</S.NoResults>
              ) : (searchResults as Product[]).length === 0 ? (
                <S.NoResults>  </S.NoResults>
              ) : (
                <S.SearchGrid>
                  {(searchResults as Product[]).map((product) => (
                    <OnboardingCard
                      key={product.id}
                      product={product}
                      isSelected={selectedIds.includes(product.id)}
                      onClick={() => toggleSelect(product.id)}
                    />
                  ))}
                </S.SearchGrid>
              )}
            </S.SearchResults>
          )}
        </S.SearchContainer>



        <S.FlowingContainer>
          {flowingProducts.map((group, groupIndex) => (
            <S.FlowingRow
              key={groupIndex}
              direction={group.direction}
              rowIndex={groupIndex}
            >
              {group.products.map((p, index) => (
                <OnboardingCard
                  key={`${p.id}-${index}`}
                  product={p}
                  isSelected={selectedIds.includes(p.id)}
                  onClick={() => toggleSelect(p.id)}
                  animationDelay={groupIndex * 0.6 + (index % 6) * 0.1}
                />
              ))}
            </S.FlowingRow>
          ))}
        </S.FlowingContainer>


        <S.CompleteButton
          disabled={selectedIds.length < 5 || isSubmitting}
          onClick={handleComplete}
        >
          {isSubmitting ? "  ..." : ` (${selectedIds.length}/5)`}
        </S.CompleteButton>
      </S.OnboardingWrapper>
      <Footer>
         2025 pusan national university capstone project Blu-ray Recommender
      </Footer>
    </NoMenuBar>
  );
};

export default function Onboarding() {
  return (
    <Suspense fallback={
      <NoMenuBar>
        <div style={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          height: '100vh',
          backgroundColor: '#fafafa'
        }}>
          <Loading
            type="enhanced"
            message="  ..."
            showProgress={true}
          />
        </div>
      </NoMenuBar>
    }>
      <OnboardingContent />
    </Suspense>
  );
}