import React, { useState } from 'react';
import { signup, getMe } from "../api/auth";
import { useAuth } from "../context/AuthContext";

export default function LoginPage() {
  const { logIn, token, me } = useAuth();
  const [username, setUsername] = useState('admin');
  const [email, setEmail] = useState('admin@naver.com');
  const [password, setPassword] = useState('password');
  const [status, setStatus] = useState('');
  const [output, setOutput] = useState('');

  const handleLogin = async () => {
    setStatus('  ...');
    setOutput('');
    try {
      await logIn(username, password);
      setStatus('  ');
      setOutput(JSON.stringify({ token, me }, null, 2));
    } catch (err: unknown) {
      const error = err as { message?: string } | undefined;
      setStatus('  ');
      setOutput(error?.message || String(err));
    }
  };

  const handleSignup = async () => {
    setStatus('  ...');
    setOutput('');
    try {
      const data = await signup(username, email, password);
      setStatus('  ');
      setOutput(JSON.stringify(data, null, 2));
    } catch (err: unknown) {
      const error = err as { response?: { data?: unknown } } | undefined;
      setStatus('  ');
      setOutput(error?.response?.data ? JSON.stringify(error.response.data, null, 2) : String(err));
    }
  };

  const handleGetMe = async () => {
    setStatus(' /me  ...');
    setOutput('');
    try {
      if (!token) {
        setStatus('  .  ');
        return;
      }
      const data = await getMe(token);
      setStatus(' /me ');
      setOutput(JSON.stringify(data, null, 2));
    } catch (err: unknown) {
      const error = err as { response?: { data?: unknown } } | undefined;
      setStatus(' /me ');
      setOutput(error?.response?.data ? JSON.stringify(error.response.data, null, 2) : String(err));
    }
  };

  return (
    <div style={{ maxWidth: 420, margin: '40px auto', padding: 20, border: '1px solid #e5e7eb', borderRadius: 10, background: '#fff' }}>
      <h1></h1>
      <input value={username} onChange={(e) => setUsername(e.target.value)} placeholder="" style={{ width: '100%', padding: 10, marginBottom: 10 }} />
      <input value={email} onChange={(e) => setEmail(e.target.value)} placeholder="" style={{ width: '100%', padding: 10, marginBottom: 10 }} />
      <input value={password} onChange={(e) => setPassword(e.target.value)} placeholder="" type="password" style={{ width: '100%', padding: 10, marginBottom: 10 }} />
      <div style={{ display: 'flex', gap: 8 }}>
        <button onClick={handleLogin} style={{ flex: 1, padding: 10, background: '#6366f1', color: '#fff' }}></button>
        <button onClick={handleSignup} style={{ flex: 1, padding: 10, background: '#0ea5e9', color: '#fff' }}></button>
        <button onClick={handleGetMe} style={{ flex: 1, padding: 10, background: '#0ea5e9', color: '#fff' }}>/me </button>
      </div>
      <p>{status}</p>
      <pre style={{ background: '#0b1020', color: '#d1d5db', padding: 10, borderRadius: 6, overflow: 'auto' }}>{output}</pre>
    </div>
  );
}
